.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 40px 0;
}

.container .card {
    position: relative;
    min-width: 250px;
    height: 350px;
    box-shadow: inset 3px 3px 3px rgba(13, 21, 47, 0.2), inset -3px -3px 10px rgba(255, 255, 255, 0.1), 3px 3px 10px rgba(13, 21, 47, 0.5), -3px -3px 10px rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    margin: 30px;
}

.container .card .box::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: rgba(67, 80, 175, 0.05);
    pointer-events: none;
}

.container .card .box {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    background: "2a2b2f";
    border: 2px solid #4350af;
    border-radius: 15px;
    box-shadow: 0 10px 50px rgba(68, 70, 175, 10);
    transition: 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.container .card .box:hover {
    transform: translateY(-50px);
    box-shadow: 0 40px 70px rgba(14, 25, 55, 0.5);
}

.container .card .box .content {
    padding: 10px;
    text-align: center;
}

.container .card .box .content h3 {
    font-size: 1.5em;
    color: rgba(255, 255, 255, 5);
    z-index: 1;
    transition: 0.5s;
    height: 60px;
}

.container .card .box .content p {
    font-size: 16px;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.5);
    z-index: 1;
    transition: 0.5s;
}

.custom-box-link {
    position: relative;
    display: inline-block;
    padding: 8px 20px;
    margin-top: 15px;
    border-radius: 20px;
    text-decoration: none;
    font-weight: 400;
    color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 8);
}

.container .card:nth-child(1) .box .content a {
    background: #8a254b;
}